/* Week5.css */
body {
  background-color: #00af75; /* Set your desired background color */
}

.week5-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  background-color: rgb(107, 218, 213);
}

.card-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 20px;
}

.card {
  border: 6px solid #2600ff;
  border-radius: 50px;
  overflow: hidden;
  background-color: #ffd92f; /* Light gray background */
  box-shadow: 0 4px 8px rgb(255, 0, 0);
  transition: box-shadow 0.3s ease-in-out, transform 0.3s ease-in-out;
}

.card:hover {
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
  transform: translateY(-3px);
}

.card-body {
  padding: 15px;
}

.card-title {
  font-size: 18px;
  margin-bottom: 10px;
  color: #333; /* Dark gray text color */
}

.card-text {
  font-size: 14px;
  margin-bottom: 5px;
  color: #666; /* Medium gray text color */
}

.age-text {
  font-size: 14px;
  color: #000000; /* White text color */
  background-color: #ffd92f; /* Tomato red background */
  padding: 5px 10px;
  border-radius: 5px;
  transition: background-color 0.3s ease-in-out;
}

.age-text:hover {
  background-color: #df9c2f; /* Darker tomato red background on hover */
}

.card-body img {
  max-width: 100%;
  height: auto;
}
